"use client";

import { useState, useTransition } from "react";
import { toast } from "sonner";
import { Button } from "@/components/ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { MoreHorizontal, PlusCircle } from "lucide-react";
import { AspForm, aspFormSchema } from "./asp-form";
import { createAsp } from "@/actions/asp-actions";
import { z } from "zod";

// aspsテーブルの型
type Asp = {
  id: string;
  name: string;
  login_url: string | null;
  media_id: string | null;
  prompt: string | null;
  created_at: string;
  updated_at: string | null;
  category?: string | null;
};

type Media = {
  id: string;
  name: string;
};

interface AgentClientProps {
  asps: Asp[];
  media: Media[];
}

type EditingAsp = {
  id: string;
  name: string;
  login_url: string;
  media_id: string;
  prompt: string;
  isNew: boolean;
};

export function AgentClient({ asps, media }: AgentClientProps) {
  const [editingRows, setEditingRows] = useState<EditingAsp[]>([]);
  const [isPending, startTransition] = useTransition();

  // メディアIDからメディア名を取得するヘルパー関数
  const getMediaName = (mediaId: string | null) => {
    if (!mediaId) return "-";
    const foundMedia = media.find((m) => m.id === mediaId);
    return foundMedia?.name || "-";
  };

  // 新しい行を追加
  const handleAddNew = () => {
    const newRow: EditingAsp = {
      id: `temp-${Date.now()}`,
      name: "",
      login_url: "",
      media_id: "",
      prompt: "",
      isNew: true,
    };
    setEditingRows([...editingRows, newRow]);
  };

  // 編集中の行を保存
  const handleSaveRow = (row: EditingAsp) => {
    if (!row.name || !row.login_url || !row.media_id || !row.prompt) {
      toast.error("全ての項目を入力してください");
      return;
    }

    startTransition(async () => {
      const result = await createAsp({
        name: row.name,
        login_url: row.login_url,
        media_id: row.media_id,
        prompt: row.prompt,
      });

      if (result.error) {
        toast.error(result.error);
      } else {
        toast.success(result.data);
        // 編集中の行を削除
        setEditingRows(editingRows.filter((r) => r.id !== row.id));
      }
    });
  };

  // 編集をキャンセル
  const handleCancelEdit = (rowId: string) => {
    setEditingRows(editingRows.filter((r) => r.id !== rowId));
  };

  // 行の値を更新
  const handleUpdateRow = (rowId: string, field: keyof EditingAsp, value: string) => {
    setEditingRows(
      editingRows.map((r) =>
        r.id === rowId ? { ...r, [field]: value } : r
      )
    );
  };

  return (
    <>
      {/* Table */}
      <div className="border rounded-lg">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="min-w-[150px]">ASP名</TableHead>
              <TableHead className="min-w-[120px]">所属メディア</TableHead>
              <TableHead className="min-w-[200px]">ログインURL</TableHead>
              <TableHead className="w-[120px]">最終実行</TableHead>
              <TableHead className="w-[80px]">ステータス</TableHead>
              <TableHead className="w-[50px]"></TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {asps.map((asp) => (
                <TableRow key={asp.id}>
                  <TableCell className="font-medium">{asp.name}</TableCell>
                  <TableCell>{getMediaName(asp.media_id)}</TableCell>
                  <TableCell>
                    {asp.login_url ? (
                      <a
                        href={asp.login_url}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-blue-600 hover:underline text-sm truncate block max-w-[200px]"
                        title={asp.login_url}
                      >
                        {asp.login_url}
                      </a>
                    ) : (
                      <span className="text-muted-foreground">-</span>
                    )}
                  </TableCell>
                  <TableCell className="text-sm text-muted-foreground">
                    {/* TODO: 実行履歴から取得 */}
                    -
                  </TableCell>
                  <TableCell>
                    {/* TODO: 実行ステータスを表示 */}
                    <span className="text-muted-foreground">-</span>
                  </TableCell>
                  <TableCell>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="ghost" className="h-8 w-8 p-0">
                          <span className="sr-only">メニューを開く</span>
                          <MoreHorizontal className="h-4 w-4" />
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end">
                        <DropdownMenuItem>編集</DropdownMenuItem>
                        <DropdownMenuItem>プロンプトを確認</DropdownMenuItem>
                        <DropdownMenuItem>今すぐ実行</DropdownMenuItem>
                        <DropdownMenuItem className="text-red-500">
                          削除
                        </DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </TableCell>
                </TableRow>
              ))}

            {/* Add new row - Notion style */}
            <TableRow className="hover:bg-muted/50">
              <TableCell colSpan={6} className="h-12 p-0">
                <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
                  <DialogTrigger asChild>
                    <button className="w-full h-full px-4 text-left text-sm text-muted-foreground hover:text-foreground flex items-center gap-2 transition-colors">
                      <PlusCircle className="h-4 w-4" />
                      <span>新規</span>
                    </button>
                  </DialogTrigger>
                  <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
                    <DialogHeader>
                      <DialogTitle>ASPを追加</DialogTitle>
                    </DialogHeader>
                    <AspForm media={media} onSubmit={onSubmit} isPending={isPending} />
                  </DialogContent>
                </Dialog>
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </div>
    </>
  );
}
